// Visualizer.js
import React, { useState, useEffect } from 'react';
import { Canvas, useLoader } from '@react-three/fiber';
import { Plane, MapControls } from '@react-three/drei';
import * as THREE from 'three';
import Papa from 'papaparse';

const Visualizer = ({ setLoading, setProgress }) => {
    const [data, setData] = useState([]);

    useEffect(() => {
      fetch('/tsne_coordinates.csv')
        .then(response => response.text())
        .then(csvString => {
          Papa.parse(csvString, {
            header: true,
            dynamicTyping: true,
            skipEmptyLines: true,
            complete: (results) => {
              const validData = results.data.filter(d => d.Image && d.x && d.y);
              setData(validData.map(d => ({ ...d, width: 1, height: 1 })));
              loadImageSizes(validData);
            }
          });
        });
    }, []);

    const loadImageSizes = (data) => {
      const imagePromises = data.map((item, index) => {
        return new Promise(resolve => {
          const img = new Image();
          img.onload = () => {
            const width = img.naturalWidth;
            const height = img.naturalHeight;
            const aspectRatio = width / height;
            resolve({ ...item, width: aspectRatio, height: 1 });
          };
          img.src = `/Images_thumb/${item.Image}`;
        });
      });

      Promise.all(imagePromises).then(completedData => {
        setData(completedData);
        setLoading(false); // Set loading to false when all images are loaded
      });

      // Update progress as images load
      imagePromises.forEach((promise, index) => {
        promise.then(() => {
          setProgress((prevProgress) => {
            const updatedProgress = (index + 1) / data.length * 100;
            return Math.round(updatedProgress);
          });
        });
      });
    };

    return (
      <Canvas 
        frameloop="demand" 
        orthographic 
        camera={{ position: [0, 0, 50], zoom: 10, up: [0, 0, 1], far: 10000 }}
        style={{ width: '100vw', height: '100vh', backgroundColor: '#000000' }}
      >
        <ambientLight intensity={0.5} />
        <pointLight position={[10, 10, 10]} />
        {data.map((d, index) => (
          <ImageThumbnail key={index} position={[d.x, d.y, 0]} width={d.width} height={d.height} imageUrl={`/Images_thumb/${d.Image}`} />
        ))}
        <MapControls />
      </Canvas>
    );
};

const ImageThumbnail = ({ position, imageUrl, width, height }) => {
  const texture = useLoader(THREE.TextureLoader, imageUrl);

  return (
    <Plane args={[width, height]} position={position} rotation={[0, 0, -Math.PI / 2]}>
      <meshBasicMaterial attach="material" map={texture} toneMapped={false} />
    </Plane>
  );
};

export default Visualizer;